footer {
  color:$black;
  font-size: .72em;

  @media (max-width:992px){

  .col-lg-2 {
      text-align: center!important;
      margin-bottom:2rem;
      &:nth-child(3){
        margin-bottom:0;
      }
    }

  }

  .col-lg-6:first-child {
    a {
      width:75px;
      margin-right:1rem;
    }
  }

  .col-lg-6:nth-child(2) {
    svg  {
     fill:$black;
     width:20px;
     &:hover{
       fill: $spaceredlight;
     }
    }
  }

  svg {
    margin:15px 5px;
  }

  ul {
    --bs-list-group-bg:none!important;
    --bs-list-group-border-color:none!important
  }

  li {
    border-top:1px solid $gray-900!important;
  }

  a {
    color:$black;
    text-decoration: none;
    &:hover {
      color:$spaceredlight;
    }
  }

  @media (max-width: 600px){

  .col-lg-2 {
      margin-bottom:0;
    }
  .row > div:first-child, .row > div:nth-child(3),  .row > div:nth-child(4){

    }
  }

  @media (max-width: 450px){

    img {
      margin-right: 0;
    }

    .col-lg-8 {
      display: block !important;
      text-align:center!important;
    }
  }



} /* end footer */
